nav {
    position: sticky;
    top: 0;
    height: 3.2em;
}

ul {
    list-style-type: none;
    margin: 0 8px 0 0;
    padding: 0;
}

.toggleable {
    /* visibility: hidden; */

}

li button {
    color: white;
    background-color: rgba(0, 0, 0, 0.9);
    border: none;
    border-radius: 8px;
    width: 4em;
    height: 4em;
    margin-bottom: 0.1em;

}

#menu-button {
    background-color: transparent;
}

@keyframes toggle {
    0% {
        visibility: hidden;
    }

    100% {
        visibility: visible;
    }
}